#modal{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    background: rgba(0,0,0,0.5);
    display: none;
    // max-height: calc(100vh - 210px);
    max-height: calc(150% - 210px);
    overflow-x: hidden;
    overflow-y: auto;
}

body.no-scroll {
    height: 100%;
    width: 100%;
    position: fixed;
    overflow: hidden;
 }
.container{
    justify-content: center;
    position:absolute;
    overflow: auto;
    overflow-x: hidden;
    overflow-y: auto;
    margin: 0vh 25vw;
    width: 700px;
    overflow: hidden;
    padding-left: 100px;
    background: white;
    box-sizing: border-box;
    padding: 15px;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
}
.container .closeModal{
    position: absolute;
    right: 15px;
    top: 15px;
}
.modalContent{
    display: none;
}
.wrapper{
    width: 600px;
    margin: 100px auto;
}
.wrapper h2{
    text-align: center;
    color: white;
    background: rga(0, 29, 38);
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    cursor: pointer;
}
.innerModal{
    width: 700px;
    height: 1176px;
    max-height: calc(100vh - 210px);
    overflow-y: auto;
}


.title{
    margin: 0;
    padding-left: 95px;
    font-size: 21px;
    line-height: 1.45;
    text-align: center;
    color: #000000;
    display: block;
    width: 500px;
}
.innerModal{
    width: 700px;
    height: 1176px;
}
.account{
    margin-bottom: 20px;
    display: flex;
}
.already{
    margin: 0;
