}
.am{
    font-size: 16px;
    line-height: 1.17;
    color: #000000;
    margin-top: 0;
    width: 550px;
    text-align: center;
}
.radio{
    margin-left: 145px;
    margin-bottom: 10px;
}
.para{
padding-right: 10px;
}
.register-form{
