    padding-left: 135px;
}
.checkbox{
    padding-left: 135px;
}
.weekly{
    font-size: 14px;
    line-height: 16px;
    color: #000000;
    font-weight: 300;
    margin-top: 0;
    margin-left: 15px;

}
.how{
    margin-top: 0;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    font-weight: 400;
    padding-left: 135px;
}
.clicking{
    font-size: 14px;
    line-height: 16px;
    color: #000000;
    font-weight: 400;
    padding-left: 135px;
}
.terms{
    font-size: 14px;
    line-height: 16px;
    color: #EF454E;
    font-weight: 400;
    padding-left: 5px;
}
.mdn{
    display: flex;
}
.button{
    width: 360px;
    height: 70px;
    background: #EF454E;
    border-radius: 4px;
    border: none;
    margin-left: 135px;
    color: #FFFFFF;
    transition: all .3s ease-out;
}
body.modal{
    overflow:hidden;
 }
 .button:hover{
    box-shadow: 3px -3px 0px #EF454E;
    border-bottom: 5px solid transparent;
    cursor: pointer;
    transition: all .3s ease-out;
    color: #fff;
 }
 .subscribe:hover{
    box-shadow: 3px -3px 0px #EF454E;
    border-bottom: 5px solid transparent;
    cursor: pointer;
    transition: all .3s ease-out;
    color: #fff;
 }
 .top{
     z-index: 1;
 }
